.store-tt {
    margin-top: 96px;
    display: flex;
    align-items: center;
}

.store-tt::before {
    content: '';
    width: 8px;
    height: 72px;
    background-color: #9f1213;
    margin-right: 16px;
}
.form-label{
    font-size: 24px;
    line-height: 36px;
}
select {
    width: 304px;
    font-size: 18px;
    line-height: 26px;
    padding: 4px 12px;
    margin-left: 16px;
    border-color: #CED4DA;
    color: #495057;
    font-family: 'Noto Sans TC';
}
@media (max-width: 992px) {
    .store-tt {
            margin-top: 80px;
    }
}
@media (max-width: 767px) {
    .store-tt {
            margin-top: 40px;
        }
    select {
        width: 80%;
        margin-left: 8px;
    }
    .store-tt::before {
        height: 36px;
    }
    .form-label {
        font-size: 16px;
        line-height: 24px;
    }
    .store {
        .container{
            margin-right: 12px;
            margin-left: 12px;
        }
    }
    .form {
        margin-top: 24px;
    }
}

.store-content {
    font-size: 20px;
    line-height: 30px;
    color: #212529;
    margin-top: 40px;
    margin-bottom: 32px;
    .card-name {
        font-size: 24px;
        line-height: 36px;
        padding: 8px 24px;
        border: 1px solid #00000020;
    }

    a{
        color: #212529;
    }
    .card-footer {
        font-size: 19px;
        line-height: 27px;
        padding: 16px;
        text-align: center;
        color: #fff;
        background-color: #000;
        display: block;
    }
}
.store-card {
    width: 33.3333%;
    padding-left: 12px;
    padding-right: 12px;
    font-family: roboto, 'Noto Sans TC';
    box-shadow: none;
    border: 0px;
    margin-bottom: 48px;

    ul {
            padding: 16px 24px;
            border: 1px solid #00000020;
    }
    li{
        margin-bottom: 8px;
    }
}
@media (max-width:992px) {
    .store-card {
        width:50%;
    }
}

@media (max-width:767px) {
    .store-content {
        margin-top: 24px;
        margin-bottom: 16px;
    }
    .store-card {
        width: 100%;
    }
}


